<template>
    <li class="list-group-item" @click="onVideoSelect">
        <img class="mr-3" :src="thumbnailUrl" />
        <div class="media-group">
            {{ video.snippet.title }}
        </div>
    </li>
</template>


<script>
    export default {
      name: 'VideoListItem',
      props: {
        video: Object
      },
      computed: {
        thumbnailUrl() {
          return this.video.snippet.thumbnails.default.url;
        }
      },
      methods: {
        onVideoSelect() {
          this.$emit('videoSelect', this.video);
        }
      }
    }
</script>

<style scoped>
    li {
        display: flex;
        cursor: pointer;
    }

    li:hover {
        background-color: #eee;
    }
</style>
